import * as React from "react";
import { useSelector, useDispatch } from 'react-redux';
import produce from "immer";

import Layout from "../components/layout"
import { Seo } from "../components/seo"

import {
    fetchContent,
    selectCaseArrayTrash,
    deleteItemFromArray
} from "../features/case/caseSlice";

import { selectApplication, loadState } from "../features/application/applicationSlice";

import Container from "react-bootstrap/Container";

import pushKeyFirebaseNode from "../features/firebase/pushKeyFirebaseNode";
import updateFirebaseNode from "../features/firebase/updateFirebaseNode";


function MyWorkbookTrash() {
    const dispatch = useDispatch();


    React.useEffect(() => {
        dispatch(fetchContent({ type: "myworkbooktrash", email: loadState()?.application.email }));
    }, [])
    return <Layout>
        <TableMoveItems />
    </Layout>
}

export default MyWorkbookTrash

export const Head = () => (<Seo title="My Workbook Trash" />)

function TableMoveItems() {
    const dispatch = useDispatch();
    const trashPosts = useSelector(selectCaseArrayTrash);
    const applicationState = useSelector(selectApplication);

    function doMovePost(item) {
        if (!!applicationState?.email) {
            let userEmail = applicationState.email.replace(/[^a-zA-Z0-9]/g, "_");
            let updates = {};
            updates["/usersCraft/" + userEmail + "/posts/" + item.id] = null;
            let newId = pushKeyFirebaseNode("/trash/");
            updates["/trash/" + newId] = { ...item, id: newId };
            console.log(updates);
            updateFirebaseNode(updates);
            dispatch(deleteItemFromArray({ arrayName: "caseArrayTrash", item: item }));
        }
    }


    return <Container>
        {trashPosts.length > 0 && trashPosts.map(item => {
            return <button key={item.id} className="btn btn-outline-danger btn-sm m-1"
                onClick={() => doMovePost(item)}
            >
                {item.title + " " + item.date + " "}
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
                    <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5Zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5Zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6Z" />
                    <path d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1ZM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118ZM2.5 3h11V2h-11v1Z" />
                </svg>
            </button>
        })
        }
    </Container>



} 
